<template>
  <div class="person_center">
    <div class="info">
      <myTitle txt="基本信息"></myTitle>
      <div class="content">
        <Tabs type="card">
          <TabPane label="修改信息">
            <!-- {{userInfo}} -->
            <div class="info_form" v-if="userInfo">
              <Form ref="formValidate"
                class="form"
                :label-width="200">
                <FormItem label="登陆名" prop="name">
                  <Input v-model="userInfo.username" placeholder="输入登陆名"></Input>
                </FormItem>
                <FormItem label="真实姓名" prop="name">
                  <Input v-model="userInfo.nickname" placeholder="输入真实姓名"></Input>
                </FormItem>
                <FormItem label="邮箱" prop="mail">
                  <Input v-model="userInfo.email" placeholder="输入邮箱"></Input>
                </FormItem>
                <FormItem label="电话号码" prop="name">
                  <Input v-model="userInfo.mobile" placeholder="输入电话号码"></Input>
                </FormItem>
                <FormItem>
                    <Button type="primary"
                      @click="btnOperate(1)">确认</Button>
                </FormItem>
              </Form>
            </div>
          </TabPane>
          <TabPane label="修改密码">
            <div class="info_form">
              <Form ref="formValidate"
                class="form" 
                :label-width="200">
                <FormItem label="旧密码" prop="name">
                  <Input v-model="passwordObj.oldPassword" type="password" 
                    password placeholder="输入旧密码" 
                    style="width: 200px" />
                </FormItem>
                <FormItem label="新密码" prop="name">
                  <Input v-model="passwordObj.newPassword" type="password" 
                    password placeholder="输入新密码" 
                    style="width: 200px" />
                </FormItem>
                <FormItem label="再次输入" prop="name">
                  <Input v-model="passwordObj.rePassword" type="password" 
                    password placeholder="再次输入新密码" 
                    style="width: 200px" />
                </FormItem>
                <FormItem>
                    <Button type="primary"
                      @click="btnOperate(2)">确认</Button>
                </FormItem>
              </Form>
            </div>
          </TabPane>
        </Tabs>
      </div>
    </div>
    <div class="sign">
      <myTitle txt="签名信息"></myTitle>
      <div class="content">
        <div class="notice">
          <span class="first">签名信息注意事项：</span>
          <span>1.图片最佳尺寸 110 * 60</span>
          <span>2.图片最佳大小不超过 20KB</span>
          <span>3.图片只支持PNG格式，透明底色</span>
        </div>
        <div class="upload">
          <Upload
            multiple
            type="drag"
            action="//jsonplaceholder.typicode.com/posts/">
            <div style="width: 110px; height: 60px;padding: 4px 0">
                <Icon type="ios-cloud-upload" size="52" style="color: #3399ff"></Icon>
            </div>
          </Upload>
        </div>
        <div class="buttons">
          <Button type="primary"
            @click="handleSubmit('formValidate')">确认</Button>
          <Button @click="handleCancel('formValidate')"
            style="margin-left: 8px">取消</Button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import myTitle from '@/components/common-title/title'
import Upload from '@/components/upload'
import {mapActions} from 'vuex'
export default {
  name: 'person_center',
  components: {myTitle},
  data () {
    return {
      userInfo: {
        id: null,
        username: null,
        nickname: null,
        mobile: null,
        email: null
      },
      passwordObj: {
        id: null,
        oldPassword: null,
        newPassword: null,
        rePassword: null,
      },
      formValidate: {
          name: '',
          mail: '',
          city: '',
          gender: '',
          interest: [],
          date: '',
          time: '',
          desc: ''
      },
      ruleValidate: {
          name: [
              { required: true, message: 'The name cannot be empty', trigger: 'blur' }
          ],
          mail: [
              { required: true, message: 'Mailbox cannot be empty', trigger: 'blur' },
              { type: 'email', message: 'Incorrect email format', trigger: 'blur' }
          ],
          city: [
              { required: true, message: 'Please select the city', trigger: 'change' }
          ],
          gender: [
              { required: true, message: 'Please select gender', trigger: 'change' }
          ],
          interest: [
              { required: true, type: 'array', min: 1, message: 'Choose at least one hobby', trigger: 'change' },
              { type: 'array', max: 2, message: 'Choose two hobbies at best', trigger: 'change' }
          ],
          date: [
              { required: true, type: 'date', message: 'Please select the date', trigger: 'change' }
          ],
          time: [
              { required: true, type: 'string', message: 'Please select time', trigger: 'change' }
          ],
          desc: [
              { required: true, message: 'Please enter a personal introduction', trigger: 'blur' },
              { type: 'string', min: 20, message: 'Introduce no less than 20 words', trigger: 'blur' }
          ]
      }
    }
  },
  created() {
    this.getInfo()
  },
  methods: {
    ...mapActions({
      getUserInfo: 'personCenter/getUserInfo',
      updateUser: 'personCenter/updateUser',
      updatePassword: 'personCenter/updatePassword'
    }),
    handleSubmit () {},
    handleCancel () {},
    btnOperate (type) {
      if (type == 2) {
        this.updateUserPwd()
      }
      if (type == 1) {
        this.updateUserInfo()
      }
    },
    // 修改用户信息 
    updateUserInfo () {
      // console.log(this.userInfo)
      // return
      this.updateUser(this.userInfo)
        .then(res => {
          console.log(res)
          this.$Message.success('操作成功！')
        })
        .catch(err => {
          console.log(err)
        })
    },
    // 修改用户密码
    updateUserPwd () {
      this.updatePassword(this.passwordObj)
        .then(res => {
          console.log(res)
          if (res.data.resp_code == 0) {
            this.$Message.success('操作成功')
          } else {
            this.$Message.error(res.data.resp_msg)
          }
        })
        .catch(err => {
          console.log(err)
          this.$Message.error(err)
        })
    },
    // 获取用户信息
    getInfo () {
      this.getUserInfo()
        .then(res => {
          console.log(res.data.datas)
          this.userInfo.id = res.data.datas.id
          this.passwordObj.id = res.data.datas.id
          this.userInfo.username = res.data.datas.username
          this.userInfo.nickname = res.data.datas.nickname
          this.userInfo.mobile = res.data.datas.mobile
          this.userInfo.email = res.data.datas.email
        })
        .catch(err => {
          console.log(err)
          this.$Message.error(err.data.resp_msg)
        })
    }
  }
}
</script>

<style lang="less" scoped>
.person_center {
  .info, .sign {
    background: #fff;
  }
  .info .form, .sign .form {
    width: 500px;
    // background: #ddd;
  }
  .info {
    .info_form {
      display: flex;
      justify-content: center;
      align-content: center;
      
    }
  }
  .sign {
    margin-top: 10px;
    text-align: center;
    .content {
      padding: 30px 0 20px 0;
    }
    .notice {
      .first {
        font-size:16px;
        font-family:PingFangSC-Regular,PingFang SC;
        font-weight:400;
        color:rgba(112,128,148,1);
      }
      span {
        font-size:14px;
        font-family:PingFangSC-Regular,PingFang SC;
        font-weight:400;
        color:rgba(156,178,205,1);
      }
    }
    .upload {
      margin-top: 20px;
      margin-bottom: 40px;
      display: flex;
      justify-content: center;
      align-content: center;
    }
  }
}
</style>